<template>
  <div class="sales-order-container">
    <el-card>
      <div slot="header" class="clearfix">
        <span>销售订单管理</span>
        <el-button type="primary" size="small" @click="handleAdd" style="float: right; margin-right: 10px">新增订单</el-button>
      </div>

      <!-- 查询条件 -->
      <el-form :inline="true" :model="searchForm" class="search-form">
        <el-form-item label="订单编号">
          <el-input v-model="searchForm.orderNumber" placeholder="请输入订单编号"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSearch">查询</el-button>
          <el-button @click="handleReset">重置</el-button>
        </el-form-item>
      </el-form>
      <!-- 表格数据 -->
      <el-table :data="tableData" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column prop="orderNumber" label="订单编号"></el-table-column>
        <el-table-column prop="customerName" label="客户名称"></el-table-column>
        <el-table-column prop="totalAmount" label="订单金额">
          <template slot-scope="scope">
            {{ scope.row.totalAmount | currency }}
          </template>
        </el-table-column>
        <el-table-column prop="status" label="订单状态">
          <template slot-scope="scope">
            <el-tag :type="getStatusType(scope.row.status)">
              {{ getStatusText(scope.row.status) }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="remark" label="描述"></el-table-column>
        <el-table-column label="操作" width="200" fixed="right">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="handleEdit(scope.row)">编辑</el-button>
            <el-button type="danger" size="small" @click="handleDelete(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="pagination-container">
        <el-pagination
          background
          :current-page="currentPage"
          :page-size="pageSize"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        ></el-pagination>
      </div>
    </el-card>
    <!-- 订单表单对话框 -->
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="90%">
      <el-form :model="form" :rules="rules" ref="orderForm" label-width="100px">
        <el-row :gutter="24">
          <el-col :span="24">
            <el-form-item label="订单编号" prop="orderNumber">
              <el-input v-model="form.orderNumber" placeholder="订单编号"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="客户" prop="customerId">
              <el-select style="width: 100%;" v-model="form.customerId" placeholder="请选择客户">
                <el-option v-for="customer in customerList" :key="customer.id" :label="customer.customerName" :value="customer.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="24">
          <el-col :span="24">
            <el-form-item label="订单金额" prop="totalAmount">
              <el-input v-model.number="form.totalAmount" placeholder="请输入订单金额"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="订单状态" prop="status">
              <el-select style="width: 100%;" v-model="form.status" placeholder="请选择订单状态">
                <el-option label="草稿" :value="0"></el-option>
                <el-option label="已审核" :value="1"></el-option>
                <el-option label="已取消" :value="-1"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="24">
            <el-form-item label="描述" prop="remark">
              <el-input v-model="form.remark" placeholder="请输入订单描述" type="textarea" :rows="2"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleDialogCancel">取消</el-button>
        <el-button type="primary" @click="handleFormSubmit">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {customerAPI, salesOrderAPI} from "@/api/api";

export default {
  name: 'SalesOrder',
  data() {
    return {
      // 查询条件
      searchForm: {
        orderNumber: '',
        customerName: '',
        orderStatus: ''
      },
      // 表格数据
      tableData: [],
      currentPage: 1,
      pageSize: 10,
      total: 0,
      // 选中的行
      selectedRows: [],
      // 对话框
      dialogVisible: false,
      dialogTitle: '',
      // 表单数据
      form: {
        id: null,
        orderNumber: '',
        customerId: null,
        totalAmount: 0,
        remark: '',
        status: 0
      },
      // 表单验证规则
      rules: {
        orderNumber: [
          { required: true, message: '订单编号不能为空', trigger: 'blur' }
        ],
        customerId: [
          { required: true, message: '请选择客户', trigger: 'blur' }
        ],
        totalAmount: [
          { required: true, message: '请输入订单金额', trigger: 'blur' }
        ]
      },
      // 客户列表
      customerList: [],
      // 其他数据

      // 当前用户信息
      currentUser: {
        id: 1,
        companyId: 1
      }
    }
  },
  mounted() {
    this.loadSelectData()
    this.loadData()
  },
  filters: {
    currency(value) {
      if (value === undefined || value === null) return '0.00'
      return parseFloat(value).toFixed(2)
    }
  },
  methods: {
    // 加载数据
    loadData() {
      const params = {
        pageNum: this.currentPage,
        pageSize: this.pageSize,
        ...this.searchForm
      }

      salesOrderAPI.page(params).then(res => {
        if (res.code === 200) {
          this.tableData = res.data.records
          this.total = res.data.total
          this.tableData.forEach(order => {
            const customer = this.customerList.find(s => s.id === order.customerId)
            console.log(this.customerList);
            if (customer) {
              order.customerName = customer.customerName
            }
          })
        } else {
          this.$message.error(res.message)
        }
      }).catch(err => {
        this.$message.error('加载数据失败')
      })
    },

    // 加载选择器数据
    loadSelectData() {
      // 加载客户列表
      customerAPI.getList().then(res => {
        if (res.code === 200) {
          this.customerList = res.data
        }
      })
    },

    // 查询
    handleSearch() {
      this.currentPage = 1
      this.loadData()
    },

    // 重置
    handleReset() {
      this.searchForm = {
        orderNumber: '',
        customerName: '',
        orderStatus: ''
      }
      this.loadData()
    },

    // 分页大小改变
    handleSizeChange(size) {
      this.pageSize = size
      this.loadData()
    },

    // 当前页改变
    handleCurrentChange(current) {
      this.currentPage = current
      this.loadData()
    },

    // 选择行变化
    handleSelectionChange(rows) {
      this.selectedRows = rows
    },

    // 获取状态类型
    getStatusType(status) {
      switch (status) {
        case 0: return 'info'
        case 1: return 'success'
        case -1: return 'danger'
        default: return 'info'
      }
    },

    // 获取状态文本
    getStatusText(status) {
      switch (status) {
        case 0: return '草稿'
        case 1: return '已审核'
        case -1: return '已取消'
        default: return '未知'
      }
    },
    // 新增
    handleAdd() {
      this.dialogTitle = '新增销售订单'
      this.form = {
        id: null,
        orderNumber: '',
        customerId: null,
        totalAmount: 0,
        remark: '',
        status: 0
      }
      this.dialogVisible = true
    },

    // 编辑
    handleEdit(row) {
      this.dialogTitle = '编辑销售订单'
      this.form = {...row}
      this.dialogVisible = true
    },

    // 查看
    handleView(row) {
      this.dialogTitle = '查看销售订单'
      this.form = {...row};
      this.dialogVisible = true
    },

    // 删除
    handleDelete(id) {
      this.$confirm('确定要删除该销售订单吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        salesOrderAPI.delete(id).then(res => {
          if (res.code === 200) {
            this.$message.success('删除成功')
            this.loadData()
          } else {
            this.$message.error(res.message)
          }
        }).catch(err => {
          this.$message.error('删除失败')
        })
      })
    },

    // 提交审核
    handleSubmit(id) {
      this.$confirm('确定要提交审核该销售订单吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        salesOrderAPI.submit(id).then(res => {
          if (res.code === 200) {
            this.$message.success('提交审核成功')
            this.loadData()
          } else {
            this.$message.error(res.message)
          }
        }).catch(err => {
          this.$message.error('提交审核失败')
        })
      })
    },

    // 取消订单
    handleCancel(id) {
      this.$confirm('确定要取消该销售订单吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        salesOrderAPI.cancel(id).then(res => {
          if (res.code === 200) {
            this.$message.success('取消订单成功')
            this.loadData()
          } else {
            this.$message.error(res.message)
          }
        }).catch(err => {
          this.$message.error('取消订单失败')
        })
      })
    },

    // 取消对话框
    handleDialogCancel() {
      this.dialogVisible = false
      this.$refs.orderForm.resetFields()
      // 启用表单（如果是查看模式）
      const inputs = document.querySelectorAll('.el-dialog input, .el-dialog select, .el-dialog textarea')
      inputs.forEach(input => {
        input.disabled = false
      })
    },

    // 提交表单
    handleFormSubmit() {
      this.$refs.orderForm.validate((valid) => {
        if (valid) {
          if (this.form.id) {
            // 编辑
            salesOrderAPI.update(this.form).then(res => {
              if (res.code === 200) {
                this.$message.success('更新成功')
                this.dialogVisible = false
                this.loadData()
              } else {
                this.$message.error(res.message)
              }
            }).catch(err => {
              this.$message.error('更新失败')
            })
          } else {
            // 新增
            salesOrderAPI.add(this.form).then(res => {
              if (res.code === 200) {
                this.$message.success('新增成功')
                this.dialogVisible = false
                this.loadData()
              } else {
                this.$message.error(res.message)
              }
            }).catch(err => {
              this.$message.error('新增失败')
            })
          }
        }
      })
    },

    // 添加明细
    handleAddItem() {
      this.form.items.push({
        id: null,
        productId: null,
        productCode: '',
        productName: '',
        unit: '',
        quantity: 1,
        unitPrice: 0,
        taxRate: 13,
        totalAmount: 0,
        taxtotalAmount: 0
      })
    },

    // 删除明细
    handleDeleteItem(index) {
      this.form.items.splice(index, 1)
      this.calculateTotal()
    },

    // 客户选择变化
    onCustomerChange() {
      const customer = this.customerList.find(c => c.id === this.form.customerId)
      if (customer) {
        this.form.customerName = customer.customerName
        this.form.customerContact = customer.contactPerson
        this.form.customerPhone = customer.contactPhone
        this.form.customerAddress = customer.address
      }
    },

    // 产品选择变化
    onProductChange(row, index) {
      const product = this.productList.find(p => p.id === row.productId)
      if (product) {
        row.productCode = product.productCode
        row.productName = product.productName
        row.unit = product.unit
        row.unitPrice = product.salesPrice || 0
        this.calculateItemtotalAmount(index)
      }
    },

    // 计算单项金额
    calculateItemtotalAmount(index) {
      const item = this.form.items[index]
      if (item.quantity && item.unitPrice) {
        // 计算不含税金额
        item.totalAmount = (item.quantity * item.unitPrice).toFixed(2)
        // 计算税额
        item.taxtotalAmount = (item.totalAmount * (item.taxRate / 100)).toFixed(2)
      } else {
        item.totalAmount = 0
        item.taxtotalAmount = 0
      }
      this.calculateTotal()
    },

    // 计算总金额
    calculateTotal() {
      let totaltotalAmount = 0
      let taxtotalAmount = 0
      this.form.items.forEach(item => {
        totaltotalAmount += parseFloat(item.totalAmount || 0)
        taxtotalAmount += parseFloat(item.taxtotalAmount || 0)
      })
      this.form.totaltotalAmount = totaltotalAmount.toFixed(2)
      this.form.taxtotalAmount = taxtotalAmount.toFixed(2)
    },

    // 获取今天日期
    getToday() {
      const today = new Date()
      const year = today.getFullYear()
      const month = String(today.getMonth() + 1).padStart(2, '0')
      const day = String(today.getDate()).padStart(2, '0')
      return `${year}-${month}-${day}`
    }
  }
}
</script>

<style scoped>
.sales-order-container {
  padding: 20px;
}

.search-form {
  margin-bottom: 20px;
}

.pagination-container {
  margin-top: 20px;
  text-align: right;
}

.table-header {
  margin-bottom: 10px;
}

.total-info {
  display: flex;
  justify-content: flex-end;
  padding: 10px;
  background-color: #f5f7fa;
  border-radius: 4px;
}

.total-item {
  margin-left: 30px;
}

.total-label {
  font-size: 14px;
  color: #606266;
}

.total-value {
  font-size: 16px;
  color: #303133;
  margin-left: 10px;
}

.total-bold {
  font-weight: bold;
  color: #1890ff;
}
</style>
